.error-page {
    &__wrapper {
        padding-top: 12rem;
        padding-bottom: 24rem;
        @media (max-width: 768px) {
            padding-top: 8rem;
            padding-bottom: 12rem;
        }
    }
    &__content {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        max-width: 1300px;
        @media (max-width: 992px) {
            flex-direction: column;
            align-items: center;
        }
    }
    &__status {
        display: flex;
        align-items: center;
        padding-left: 20px;
        @media (max-width: 992px) {
            margin-bottom: 40px;
            padding-left: 0;
        }
        @media (max-width: 768px) {
            max-width: 100%;
            margin-bottom: 30px;
        }
        .four {
            @include containBg();
            width: 160px;
            min-width: 160px;
            height: 216px;
            background-image: url(../images/4.svg);
            mix-blend-mode: color-dodge;
            @media (max-width: 768px) {
                width: 100px;
                min-width: unset;
                height: 120px;
            }
        }
        .zero {
            @include containBg();
            width: 166px;
            min-width: 166px;
            height: 224px;
            background-image: url(../images/0.svg);
            mix-blend-mode: exclusion;
            margin: 0 24px;
            @media (max-width: 768px) {
                width: 100px;
                min-width: unset;
                height: 120px;
            }
        }
        span {
            display: block;
        }

    }

    &__text {
        max-width: 506px;
        padding-left: 24px;
        @media (max-width: 992px) {
            padding-left: 0;
            text-align: center;
        }
        .title {
            font-size: 3.2rem;
            line-height: 1.5;
            text-transform: uppercase;
            font-weight: 700;
            color: $orange-dim;
        }
        .description {
            font-size: 1.8rem;
            line-height: 1.44;
            color: $white;
            margin-top: 16px;
        }
        .link {
            display: block;
            width: 200px;
            margin-top: 64px;
            @media (max-width: 992px) {
                margin: 50px auto 0;
            }
            @media (max-width: 768px) {
                margin: 30px auto 0;
            }
        }
    }
}